import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './components/app/App';
import * as serviceWorker from './serviceWorker';

import login from "./components/Login/login";
import register from './components/register/register';
import Home from './components/Home/Home';
import Quizz from './components/quizz/quizz'
import QuizzDo from './components/quizz/quizzDo'
import QuizzPost from './components/quizz/quizzPost'
import { Router, Route, Switch } from 'react-router-dom';
import history from './history';
import MapTest5 from './components/maps/MapTest5';
import logout from './components/logout/logout';
import MapTest6 from './components/maps/MapTest6';
import QMCPost from './components/quizz/QMCPost';
import QOPost from './components/quizz/QOPost';
import QTFPost from './components/quizz/QTFPost';
import Trail from './components/trail/Trail';
import TrailList from './components/trail/TrailList';


ReactDOM.render(
    <Router history={history}>
      <Switch>
      <Route exact path={"/"} component={App} />
      <Route path={"/login"} component={login} />
      <Route path={"/logout"} component={logout}/>
      <Route path={"/register"} component={register} />
      <Route path={"/home"} component={Home} />
      <Route path={"/trail"} component={Trail}/>
      <Route path={"/postTrail"} component={MapTest5} />
      <Route path={"/viewlist"} component={TrailList}/>
      <Route path={"/view/:name"} component={MapTest6} />
      <Route path={"/askquizz"} component={Quizz}/>
      <Route path={"/quizz/create"} component={QuizzPost}/>
      <Route path={"/quizz/createQMC"} component={QMCPost}/>
      <Route path={"/quizz/createQO"} component={QOPost}/>
      <Route path={"/quizz/createQTF"} component={QTFPost}/>
      <Route path={"/quizz/start"} component={QuizzDo}/>
      </Switch>
    </Router>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();