/*global google*/
import React, {Component} from 'react';
import { GoogleMap, withGoogleMap,withScriptjs,Marker,DirectionsRenderer} from "react-google-maps";
import { withRouter } from 'react-router-dom';
import _ from 'lodash';

const Map = withScriptjs(
    withGoogleMap(props => (
        <GoogleMap
        defaultZoom={10} 
        defaultCenter={{lat:38.7071, lng: -9.13549}}
        >
            {props.marks.map((mark, index) => (
                <Marker
                key={index}
                position={mark}
            />
            ))}
        {props.directions && <DirectionsRenderer directions={props.directions} />}
        </GoogleMap>
    ))
);


class MapTest6 extends Component{
    constructor(props){
        super(props);
        this.state={
            directions: null,
            marks: [],
            name: props.match.params.name,
            description: '',
            start: '',
            end: '',
            dist: '',
        };
    }


    getDirection = () => {
        const directionsService = new google.maps.DirectionsService();
        const markers = this.state.marks;
        this.setState({
            directions: null,
        });
        const origin = markers[0];
        const destination = markers[Object.keys(markers).length-1];
        const waypts = [];

        for( let i=1; i<Object.keys(markers).length-1;i++){
            let location = {
                location: markers[i],
            };
            waypts.push(location);
        }

        directionsService.route({
            origin: origin,
            destination: destination,
            travelMode: google.maps.TravelMode.DRIVING,
            waypoints: waypts,
          }, (result, status) => {
            if (status === google.maps.DirectionsStatus.OK) {
              this.setState({
                directions: result,
              });
              let distance = _.flatMap(result.routes, route => _.flatMap(route.legs, leg => leg.distance.value));  
              let sum = _.sum(distance);
              sum = sum/1000;
              this.setState({dist:sum});
            } else {
              console.error(`error fetching directions ${result}`);
            }
          });
    }

    async load(name){
        await fetch('/gettrail/'+name, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
            },
        })
        .then(function(response){ 
            if(response.ok)
            //receber as cenas
                return; 
            else
                //erro
                return;
            })
    };
 


    render(){
        return(
            <div style={{width:'100vw', height:'100vh'}}>
                <Map
                googleMapURL="http://maps.googleapis.com/maps/api/js?key=AIzaSyD2HdYk7gSqjAnTchqAL4EilOOBWLjPExA"
                loadingElement={<div style={{ height: `100%` }} />}
                containerElement={<div style={{ height: `100%` }} />}
                mapElement={<div style={{ height: `100%` }} />}
                marks={this.state.marks}
                directions={this.state.directions}
                >
                </Map>
            </div>
        );
    }
}

export default withRouter(MapTest6);