import React,{ Component } from "react";
import { withRouter } from "react-router-dom";
import history from '../../history';

import './quizzDo.css';
import QuizzQMC from "./QuizzQMC";
import QuizzQO from "./QuizzQO";
import QuizzQTF from "./QuizzQTF";

var randomN;
var randomIndex;

var questionQMC = {enunciated:"qual e a espesura da terra",
                   question:"escolha a opcao correta",
                   optionA:"50 km 50 km50 km50 km50 km50 km50 km50 km50 km50 km50 km50 km50 km",
                   optionB:"35 km",
                   optionC:"1 km",
                   optionD:"100 km",
                   correctOption:"B",
                   id:5
                };

var questionQO = {
                    enunciated: "Ordena as frases de modo a reconstituir a .",
                    question: "Ordena as frases",
                    options: {
                        options: ["Incio de subduo de crosta ocenica, geralmente mais antiga e mais densa." ,
                                    "Incio do alargamento de um oceano primitivo.", 
                                    "Formao de cadeias montanhosas de coliso.",
                                    "Instalao de vulcanismo andestico em margens continentais ativas.",
                                    "Estiramento de crosta continental."]
                    },
                    byOrder:{
                        byOrder:["option5", "option2", "option1", "option4", "option3"]
                    },
                    id:""
                };

var questionQTF = {
                    enunciated: "A Lua  o satlite da Terra que foi.",
                    question: "Classifique como verdadeira ou falsa cada uma das seguintes afirmaes, relativas a caractersticas da Lua.",
                    questionsList: {
                        questions: [  "H mais de 3000 M.a., a Lua era um planeta geologicamente activo.", 
                                      "Terrae (os continentes) so zonas claras, ricas em minerais flsicos.",
                                      "A eroso tem reduzido o nmero de crateras de impacto nos mares.",
                                      "Maria (os mares) so zonas escuras, ricas em minerais ferromagnesianos."]
                    },
                    answersList:{
                        answersList:["T", "T", "F", "T"]
                    },
                    id:1,
                    numberOfQuestions:4
                };

function randomNumber(min,max){
    let step1 = max - min + 1;
    let step2 = Math.random() * step1;
    let result = Math.floor(step2) + min;
    return result;
}

function arrayRandomNumber(start, end){
    let array = [];

    for(let i = start; i >= end; i++){
        array.push(i);
    }

    return array;
}

let arrayID = arrayRandomNumber(1,23);

const quizzesQMC = [];
const quizzesQO = [];
const quizzesQTF = [];

class quizzDo extends Component{

    constructor(props){
        super(props);
        /*for(let i=0;i<7;i++){
            this.askquizzQMC();
        }
        this.askquizzQO();
        this.askquizzQTF();
        this.state={
            answer:[],
        }*/
        for(let i=0;i<6;i++){
            quizzesQMC.push(questionQMC);
        }
        quizzesQO.push(questionQO);
        quizzesQTF.push(questionQTF);
        this.state={
            answer:[
                '',
                '',
                '',
                '',
                '',
                '',
                '',
                '',
            ],
            result: '',
              
        }
    }

    DefaultError() {
        this.setState({ error: '' });
    }


    validateQOs(i,j){
        var bol1= false;
        var bol2= false;
        var bol3= false;
        var bol4= false;
        var bol5= false;

        if(quizzesQO[i].byOrder.byOrder[0] == this.state.answer[j][0])
            bol1=true;

        if(quizzesQO[i].byOrder.byOrder[1] == this.state.answer[j][1])
            bol2=true;

        if(quizzesQO[i].byOrder.byOrder[2] == this.state.answer[j][2])
            bol3=true;

        if(quizzesQO[i].byOrder.byOrder[3] == this.state.answer[j][3])
            bol4=true;

        if(quizzesQO[i].byOrder.byOrder[4] == this.state.answer[j][4])
            bol5=true;

        return bol1 && bol2 && bol3 && bol4 && bol5;
    }

    validateQTFs(i,j){
        var q1=false;
        var q2=false;
        var q3=false;
        var q4=false;

        if(quizzesQTF[i].answersList.answersList[0] == this.state.answer[j][0])
            q1=true;

        if(quizzesQTF[i].answersList.answersList[1] == this.state.answer[j][1])
            q2=true;

        if(quizzesQTF[i].answersList.answersList[2] == this.state.answer[j][2])
            q3=true;

        if(quizzesQTF[i].answersList.answersList[3] == this.state.answer[j][3])
            q4=true;

        return q1 && q2 && q3 && q4;
    }

    validateQMC(i,j){
        if(quizzesQMC[i].correctOption == this.state.answer[j])
            return true;
        return false;
    }

    gotoApp(){
        history.goBack();
    }


    async askquizzQMC(){
        if(arrayID.length == 0){
            arrayID = arrayRandomNumber(1,23);
        }
        randomIndex = randomNumber(0,arrayID.length-1);
        randomN = arrayID[randomIndex];
        arrayID.splice(randomIndex,1);
        await fetch('https://trailobyte-275015.ew.r.appspot.com/rest/question/getQMC/'+ randomNumber, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
            }
        })
        .then(function(data) {
            console.log(data);
            quizzesQMC.push(data);
        })
        
        }

    async askquizzQO(){
        if(arrayID.length == 0){
            arrayID = arrayRandomNumber(1,23);
        }
        randomIndex = randomNumber(0,arrayID.length-1);
        randomN = arrayID[randomIndex];
        arrayID.splice(randomIndex,1);
        await fetch('https://trailobyte-275015.ew.r.appspot.com/rest/question/getQO/'+ randomNumber, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
            }
        })
        .then(function(data) {
            console.log(data);
            quizzesQO.push(data);
        })
    }



    async askquizzQTF(){
        if(arrayID.length == 0){
            arrayID = arrayRandomNumber(1,23);
        }
        randomIndex = randomNumber(0,arrayID.length-1);
        randomN = arrayID[randomIndex];
        arrayID.splice(randomIndex,1);
        await fetch('https://trailobyte-275015.ew.r.appspot.com/rest/question/getQTF/'+ randomNumber, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
            }
        })
        .then(function(data) {
            console.log(data);
            quizzesQTF.push(data);
        })
    }

    callBackAnswers = answerFromQuestion => {
        var array = this.state.answer;
        array[answerFromQuestion[0]-1] = answerFromQuestion[1];
        this.setState({answer: array});
    }

    callbacktest = () => {
        var result = 0;
        this.refs.Question.activate();
        this.refs.Question1.activate();
        this.refs.Question2.activate();
        this.refs.Question3.activate();
        this.refs.Question4.activate();
        this.refs.Question5.activate();
        this.refs.Question6.activate();
        this.refs.Question7.activate();
        for(let i = 0; i<6; i++){
            if(this.validateQMC(i,i))
                result++;
        }
        if(this.validateQOs(0,6))
            result++;
        if(this.validateQTFs(0,7))
            result++;
        console.log(this.state.answer);
        console.log(result);
    }


    render(){
        return(
            <div className="quizzDo">
                <button onClick={this.gotoApp}>Back</button>
        
                <h1>Quizz</h1>
                <div className="allQuestions">
                   <h2>Pergunta 1</h2>
                   <QuizzQMC nr={1} question = {quizzesQMC[0]} parentCallback = {this.callBackAnswers} ref ="Question"></QuizzQMC>
                   <h2>Pergunta 2</h2>
                   <QuizzQMC nr={2} question = {quizzesQMC[1]} parentCallback = {this.callBackAnswers}  ref ="Question1"></QuizzQMC>
                   <h2>Pergunta 3</h2> 
                   <QuizzQMC nr={3} question = {quizzesQMC[2]} parentCallback = {this.callBackAnswers}  ref ="Question2"></QuizzQMC>
                   <h2>Pergunta 4</h2>  
                   <QuizzQMC nr={4} question = {quizzesQMC[3]} parentCallback = {this.callBackAnswers}  ref ="Question3"></QuizzQMC>
                   <h2>Pergunta 5</h2>  
                   <QuizzQMC nr={5} question = {quizzesQMC[4]} parentCallback = {this.callBackAnswers}  ref ="Question4"></QuizzQMC>
                   <h2>Pergunta 6</h2>    
                   <QuizzQMC nr={6} question = {quizzesQMC[5]} parentCallback = {this.callBackAnswers}  ref ="Question5"></QuizzQMC>
                   <h2>Pergunta 7</h2>    
                   <QuizzQO nr={7} question = {quizzesQO[0]} parentCallback = {this.callBackAnswers}  ref ="Question6"></QuizzQO>
                   <h2>Pergunta 8</h2>    
                   <QuizzQTF nr={8} question = {quizzesQTF[0]} parentCallback = {this.callBackAnswers}  ref ="Question7"></QuizzQTF>
                   <input type="submit" onClick ={this.callbacktest} value = "Submit"/>         
                </div>
            </div>
        );
    }
}
export default withRouter(quizzDo);